import React from "react"
import Firebase from "../utils/firebase"
import { Helmet } from "react-helmet"

class ReviewText extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      review: this.props.review.slice(0, 80),
      isTooLong: this.props.review.length > 80,
    }
  }

  onClick = () => {
    if (this.state.review.length > 80) {
      this.setState({
        review: this.props.review.slice(0, 80),
      })
    } else {
      this.setState({
        review: this.props.review,
      })
    }
  }

  render() {
    return (
      <div>
        {this.props.review.length > 80 && this.state.review.length <= 80
          ? this.state.review + "..."
          : this.state.review}
        {this.state.isTooLong && (
          <div>
            <button
              style={{
                backgroundColor: "transparent",
                border: "none",
                cursor: "pointer",
                textDecoration: "underline",
                display: "inline",
                margin: "0",
                padding: "0",
                color: "#1ca086",
                fontSize: "60%",
              }}
              onClick={this.onClick}
            >
              {this.state.review.length > 80 ? "Show less" : "Show more"}
            </button>
          </div>
        )}
      </div>
    )
  }
}

class Reviews extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      reviews: [],
      isSignedIn: false,
      myReview: "",
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  componentDidMount() {
    this.firebase = new Firebase()
    var firebaseui = require("firebaseui")

    this.db = this.firebase.db
    this.auth = this.firebase.auth
    this.ui = new firebaseui.auth.AuthUI(this.auth);

    this.db
      .collection("toilets")
      .doc(this.props.name)
      .onSnapshot(doc => {
        this.setState({
          reviews: doc.data().reviews,
        })
      })

    this.uiConfig = {
      signInOptions: [
        this.firebase.firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      ],
      signInFlow: "popup",
      callbacks: {
        // Avoid redirects after sign-in.
        signInSuccessWithAuthResult: () => false,
      },
    }
    this.ui.start("#firebaseui-auth-container", this.uiConfig)

    this.auth.onAuthStateChanged(user => {
      this.setState({ isSignedIn: !!user })
    })
  }

  handleChange(event) {
    this.setState({ myReview: event.target.value })
  }

  handleSubmit(event) {
    event.preventDefault()
    this.db
      .collection("toilets")
      .doc(this.props.name)
      .set(
        {
          reviews: this.firebase.firebase.firestore.FieldValue.arrayUnion({
            name: this.auth.currentUser.displayName,
            review: this.state.myReview,
            date: new Date(),
          }),
        },
        { merge: true }
      )

    this.setState({
      myReview: "",
    })
  }

  render() {
    return (
      <div>
        <Helmet>
          <link
            type="text/css"
            rel="stylesheet"
            href="https://cdn.firebase.com/libs/firebaseui/3.5.2/firebaseui.css"
          />
        </Helmet>
        <h4>Reviews</h4>
        {!this.state.isSignedIn && (
          <div>
            <div id="firebaseui-auth-container" />
            <p
              style={{ fontSize: "0.7em", color: "gray", textAlign: "center" }}
            >
              Sign in with Google to post a review!
            </p>
          </div>
        )}
        {this.state.isSignedIn && (
          <div>
            <p>
              Welcome {this.auth.currentUser.displayName}! You are now
              signed-in!{" "}
              <button
                onClick={() => {
                  this.auth.signOut()
                  window.location.reload()
                }}
              >
                Sign-out
              </button>
            </p>
            <form onSubmit={this.handleSubmit}>
              <label>
                Review:
                <textarea
                  style={{ width: "100%", resize: "none", height: "75px" }}
                  value={this.state.myReview}
                  onChange={this.handleChange}
                />
              </label>
              <input
                type="submit"
                value="Submit"
                disabled={this.state.myReview.trim() === ""}
              />
            </form>
          </div>
        )}
        <table style={{ tableLayout: "fixed" }}>
          <tbody>
            {this.state.reviews.length === 0 ? (
              <tr key={1}>
                <td>
                  <p>No reviews (yet!)</p>
                </td>
              </tr>
            ) : (
              this.state.reviews
                .map((x, i) => {
                  return (
                    <tr key={i}>
                      <td>
                        <strong style={{ color: "blue", fontSize: "80%" }}>
                          {x.name}
                        </strong>
                        <span style={{ color: "gray", fontSize: "80%" }}>
                          {" â€¢ " +
                            x.date.toDate().toLocaleString("default", {
                              day: "numeric",
                              month: "long",
                              year: "numeric",
                              hour: "numeric",
                              minute: "numeric",
                            })}
                        </span>
                        <br />
                        <ReviewText review={x.review} />
                      </td>
                    </tr>
                  )
                })
                .reverse()
            )}
          </tbody>
        </table>
      </div>
    )
  }
}

export default Reviews
